.drawer {
  position: fixed;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #ffffff;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
  z-index: 200;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
  width: 280px;
}

.drawer.open {
  transform: translateX(0);
}

