      width: 70%;
    }
  
    .description__svg {
      margin-top: 20px;
    }
    .description__rightContent {
      width: 100%;
      margin-left: 20px;
      margin-top: 15px;
    }
    .description__submit {
      margin-left: 9px;
    }
  }
  
