.assignmentCards-a {
  position: relative;
  background-color: #fff;
  cursor: pointer;
  width: 220px !important;
  height: 130px;
  overflow: hidden;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.08);
  color: #033443;
  border: 1px solid #ebecee;
  border-radius: 16px;
  letter-spacing: normal;
  transition: all 0.2s ease-out;
}

.assignmentCards > img {
  position: absolute;
  object-fit: contain;
  width: 100%;
  top: 0;
  left: 0;
}

.assignmentCards:hover {
  transform: scale(1.06);
}

.assignmentCards > h4 {
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 600;
  margin-top: 100px;
  margin-left: 15px;
}

/* SKELETON */
.assignmentCards__skeleton {
  position: relative;
  background-color: #fff;
  cursor: pointer;
  width: 220px;
  height: 130px;
  overflow: hidden;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.08);
  color: #033443;
  border: 1px solid #ebecee;
  border-radius: 16px;
  letter-spacing: normal;
  transition: all 0.2s ease-out;
  padding: 10px;
  /* padding-top: 10px;
    padding-left: 10px; */
}

.cardSkeleton_logo {
  margin-bottom: 10px;
}

